    h4.mb-5 Costo 
    p Es el valor que se genera al producir un bien o prestar un servicio, el costo es recuperable al realizar la venta de este. Para identificar el costo es fundamental conocer el tipo de empresa, la cual puede ser comercial, industrial y de servicios; conforme a ello se identifica lo que cuesta el bien o servicio. A continuacin, se observa el comparativo de cada una de estas mediante un ejemplo:

