    #t_1_6.titulo-segundo
      h2 1.6 Diferencia entre contabilidad de costos y contabilidad financiera
    p La contabilidad de costos, tambin conocida como contabilidad analtica, es una tcnica contable que tiene como finalidad crear un sistema de informacin que permita conocer cul es el costo de los productos fabricados. Es un instrumento que sirve de apoyo a la contabilidad financiera, estudiando la estructura de costes en las empresas. La contabilidad de costes consiste en realizar una imputacin razonable de costes directos e indirectos que permita obtener informacin analtica en la que apoyarse para la toma de decisiones por parte de la direccin de la empresa (Sinisterra, 2017, p. 2). 
    .row.justify-content-around.align-items-center
      .col-lg.justify-content-md-center.align-self-center
        p La contabilidad financiera es una disciplina que consiste en recopilar, ordenar y registrar la informacin de la actividad econmica de una empresa. Es una rama del rea de la contabilidad que se ocupa de sistematizar la informacin de las actividades y la situacin econmica de una empresa en un momento del tiempo y a lo largo de su desarrollo (Contabilidad financiera, 2021).
        p La contabilidad financiera permite tener un registro histrico y cuantificable de las actividades que realiza una empresa y de los eventos econmicos que le afectan.


      .col-12.col-lg-auto.justify-content-md-center.mb-4.mx-5
        img.img-300(src="@/assets/tema1/img-6.svg") 
