    #t_1_7.titulo-segundo
      h2 1.7 Normas contables aplicables a los costos de ventas y produccin
    p En este contexto es fundamental tener en cuenta la Norma Internacional de Contabilidad (NIC) 2 Inventarios, que se encuentra compuesta as: 
    p.r-n.text-bold  Elementos de la NIC 2

    .row.justify-content-around.align-items-center.mb-5
