    p.mb-5 Segn la NIC 2, el costo de inventarios incluye el costo de adquisicin, costos de transformacin y otros costos atribuibles. Cabe resaltar que los costes de transformacin de las existencias comprendern aquellos costes directamente relacionados con las unidades producidas, tales como la mano de obra directa. Tambin comprendern una parte, calculada de forma sistemtica, de los costes indirectos, variables o fijos, en los que se haya incurrido para transformar las materias primas en productos terminados (NIC 2, s.f., pp. 1-8).

    h3 Mtodos de valoracin         
    p Dentro de los mtodos de valoracin de inventarios se encuentran:
    p.r-n.text-bold Mtodos de valoracin NIC 2
    .row.justify-content-around.align-items-center.mb-5
