    h3.mb-5 Valor neto realizable
    p.mb-5 Es el precio estimado de venta de un activo en el curso normal de la explotacin, menos los costes estimados para terminar su produccin y los necesarios para llevar a cabo la venta (Varn, 2017, prr. 2).

    .row.justify-content-around.align-items-center.mb-5
      .col-lg-10.justify-content-md-center.align-self-center        
        .cajon.color-primario.p-4.mb-4.fond2
            .h5 Valor neto realizable
            p Costo de los inventarios puede no ser recuperable en caso de que los mismos estn daados, si han devenido parcial o totalmente obsoletos, o bien si sus precios de mercado han cado. Asimismo, el costo de los inventarios puede no ser recuperable si los costos estimados para su terminacin o su venta han aumentado. La prctica de rebajar el saldo, hasta que el costo sea igual al valor neto realizable, es coherente con el punto de vista segn el cual los activos no deben registrarse en libros por encima de los importes que se espera obtener a travs de su venta o uso.
    p.mb-5 Los costos indirectos de fabricacin hacen parte de los costos de transformacin, segn se describe en los prrafos 12 a 14 de la NIC 2 Inventarios, para conocerlos se invita a ingresar en el siguiente sitio:              
         
