    h3 Norma Internacional de Contabilidad (NIC) 2 Inventarios                    
    .row.justify-content-around.align-items-center
      .col-lg.justify-content-md-center.align-self-center
        p En abril de 2001 el Consejo de Normas Internacionales de Contabilidad (Consejo) adopt la NIC 2 Inventarios, que haba sido originalmente emitida por el Comit de Normas Internacionales de Contabilidad en diciembre de 1993. La NIC 2 Inventarios reemplaz a la NIC 2 Valoracin y presentacin de inventarios en el contexto del sistema de costo histrico (originalmente emitida en octubre de 1975). En diciembre de 2003 el Consejo emiti una NIC 2 revisada como parte de su agenda inicial de proyectos tcnicos. La NIC 2 revisada tambin incorpor las guas contenidas en una Interpretacin relacionada (SIC-1 Uniformidad - Diferentes frmulas para el clculo del costo de los inventarios). 
        p Otras normas han realizado modificaciones consiguientes de menor importancia a la NIC 2. Estas incluyen la NIIF 13 Medicin del valor razonable (emitida en mayo de 2011), la NIIF 9 Instrumentos financieros (contabilidad de coberturas y modificaciones a las NIIF 9, NIIF 7 y NIC 39) (emitida en noviembre de 2013), NIIF 15 Ingresos de actividades ordinarias procedentes de contratos con clientes (emitida en mayo de 2014), NIIF 9 Instrumentos financieros (emitida en julio de 2014) y NIIF 16 Arrendamientos (emitida en enero de 2016) (NIC, s.f., pp. 4-16).

      .col-12.col-lg-auto.justify-content-md-center.mb-4 
        figure    
            img.img-450(src="@/assets/tema1/img-11.png")  

    .row.justify-content-around.align-items-center

      .col-12.col-lg-auto.justify-content-md-center.mb-4 
        figure    
            img.img-450(src="@/assets/tema1/img-12.svg")     
      .col-lg.justify-content-md-center.align-self-center.mb-3           
        p #[strong Objetivo:] el objetivo de esta norma es prescribir el tratamiento contable de los inventarios. Un tema fundamental en la contabilidad de los inventarios es la cantidad de costo que debe reconocerse como un activo, para que sea diferido hasta que los ingresos correspondientes sean reconocidos. Esta norma suministra una gua prctica para la determinacin de ese costo, as como para el subsiguiente reconocimiento como un gasto del periodo, incluyendo tambin cualquier deterioro que rebaje el importe en libros al valor neto realizable. Tambin suministra directrices sobre las frmulas del costo que se usan para atribuir costos a los inventarios (NIC, s.f., pp. 4-16).
        p #[strong Alcance:] esta norma se aplica a todos los inventarios, excepto a: (a) [eliminado] (b) los instrumentos financieros (vanse las NIC 32 Instrumentos financieros: presentacin y NIIF 9 Instrumentos financieros); y (c) los activos biolgicos relacionados con la actividad agrcola y productos agrcolas en el punto de cosecha o recoleccin (ver la NIC 41 Agricultura). 
        p Esta norma no se aplica en la medicin de los inventarios mantenidos por: a) Productores de productos agrcolas y forestales, de productos agrcolas tras la cosecha o recoleccin, de minerales y de productos minerales, siempre que sean medidos por su valor neto realizable, de acuerdo con prcticas bien consolidadas en esos sectores industriales. 

    p En el caso de que esos inventarios se midan al valor neto realizable, los cambios en este valor se reconocern en el resultado del periodo en que se produzcan dichos cambios. b) Intermediarios que comercian con materias primas cotizadas, siempre que midan sus inventarios al valor razonable menos costos de venta. En el caso de que esos inventarios se lleven al valor razonable menos costos de venta, los cambios en el importe del valor razonable menos costos de venta se reconocern en el resultado del periodo en que se produzcan dichos cambios. 

    p Los inventarios a que se ha hecho referencia se miden por su valor neto realizable en ciertas fases de la produccin. Ello ocurre, por ejemplo, cuando se han recogido las cosechas agrcolas o se han extrado los minerales, siempre que su venta est asegurada por un contrato a plazo sea cual fuere su tipo o garantizada por el gobierno, o bien cuando existe un mercado activo y el riesgo de fracasar en la venta sea mnimo. Esos inventarios 1. 2. 3. 4. NIC 2 A1062  IFRS Foundation se excluyen nicamente de los requerimientos de medicin establecidos en esta Norma.

    p.mb-5 Los intermediarios que comercian son aquellos que compran o venden materias primas cotizadas por cuenta propia, o bien por cuenta de terceros. Los inventarios a que se ha hecho referencia en el apartado b se adquieren, principalmente, con el propsito de venderlos en un futuro prximo, y generar ganancias procedentes de las fluctuaciones en el precio, o un margen de comercializacin. Cuando esos inventarios se contabilicen por su valor razonable menos los costos de venta quedarn excluidos nicamente de los requerimientos de medicin establecidos en esta Norma (NIC, s.f., pp. 4-16).

