    #t_1_8.titulo-segundo
      h2 1.8 Elementos de costo
    p.mb-5 Los elementos de costo de produccin intervienen en una empresa industrial o manufacturera que se encarga de transformar materia prima en productos terminados. La sumatoria de los conceptos de MPD; MOD y CIF es lo que se considera como costo de produccin. 
    .row.justify-content-around.align-items-center.mb-3
      .col-lg-10.justify-content-md-center.align-self-center        
        .cajon.color-primario.p-4.mb-4.fond2
            p La fabricacin de artculos o productos acabados requiere de la intervencin de ciertos factores, elementos importantes que expresan el costo de produccin de estos. Una industria de transformacin incurre en costos como son: consumo de materias primas y/o materiales directos, uso de la mano y la inteligencia del hombre (fsico-mental), y una serie de costos adicionales que son imprescindibles para la conclusin de una obra, entre otros se menciona: uso de maquinaria, equipos, herramientas, espacios fsicos, etc., con los que es posible llevar a cabo el proceso de transformacin (Solo contabilidad, s.f., p. 1).
    p En consecuencia, el costo de produccin de modo general est formado por tres elementos:  

    .row.mb-5
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
