            .p-4
              p.text-center #[strong Materia prima y/o material directo]
              p Para ser considerado materiales directos debe ser de fcil identificacin en cada unidad de producto.
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/tema1/img-14.svg' alt='AvatarTop')

          .tarjeta.fondo5
            .p-4
              p.text-center #[strong Sueldos y salarios y/o obra de mano]
              p Se refiere a los salarios de los trabajadores por la transformacin de los materiales en productos acabados.      

      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/tema1/img-15.svg' alt='AvatarTop')

          .tarjeta.fondo5
            .p-4
              p.text-center #[strong Gastos indirectos de produccin]
              p Considerados como el tercer elemento del costo, son los que no se identifican fcilmente en un producto (Solo contabilidad, s.f., p. 1).    

    p Ahora, se presenta el ejemplo de una empresa manufacturera para su respectivo anlisis:
    p La empresa Snchez S.A.S. dedicada a la produccin de muebles de madera requiere los siguientes elementos:
    .ul.lista-ul--color.mx-5
      li 
        i.fas.fa-check
        p #[strong MPD:] madera, pintura, sellantes, tornillos, materiales de tapicera, etc.
      li 
        i.fas.fa-check
        p #[strong MOD:] salarios de los operarios como cortadores, armadores, pintores, cepilladores, etc.
      li 
        i.fas.fa-check
        p #[strong CIF:] Depreciacin y mantenimiento de la mquina, servicios pblicos de la planta de produccin, supervisores, jefes de planta.

    p.mb-5 A continuacin, se profundizar en los elementos del costo. 

    h3 Materia prima y/o material directo
    p Son los elementos que estn sujetos a transformacin y conviene diferenciarlos de la siguiente manera:

    .row.justify-content-around.align-items-center
      .col-10
        .crd_01.mb-5
          .crd_01__icon
            img(src="@/assets/tema1/img-16.svg", alt="alt")          
          .crd_01__txt
            p.text-bold Materia prima
            p Son los elementos extrados de la naturaleza que es la fuente de aprovisionamiento de todos los elementos que son necesarios para satisfacer necesidades humanas o convertirse en el primer elemento del costo para las empresas industriales. Se pueden citar como ejemplos: el hierro en su estado natural, el algodn en la fbrica de hilo o telas, los callapos o bolillos para la obtencin de madera en los aserraderos, etc.; es decir, en las empresas tomadas como ejemplo se puede denominar el primer elemento de costo como materia prima (Solo contabilidad, s.f. p.1).

        .crd_01.mb-5
          .crd_01__icon
            img(src="@/assets/tema1/img-17.svg", alt="alt")          
          .crd_01__txt
            p.text-bold Material directo
            p Se llama as a los elementos que son sometidos a uno o varios procesos de transformacin por otra empresa, los cuales sern sujetos de otros procesos de transformacin hasta convertirlos en un producto acabado apto para satisfacer las necesidades humanas. Se hace esta diferenciacin debido a que se tiene una confusin al usar en forma indiscriminada el denominativo de materia prima, por lo tanto, ser costo de materia prima para todas las empresas que utilizan los elementos que an no han sido sometidos a procesos de transformacin, y para aquellas que utilizan como principal elemento de transformacin los productos sometidos a procesos de manufactura por otra entidad, se denominar material directo.
            p En general, material comprende todos aquellos artculos en su estado natural o transformado son los elementos que sirven para la confeccin o manufactura de un artculo nuevo y que pueden ser perfectamente medibles y por esta razn son cargables a una unidad identificada; por lo tanto, es el primer elemento del costo de produccin.
    br
    h3 Mano de obra
    p.mb-5 Para transformar la materia prima y/o material directo en un producto acabado es necesario, adems de la maquinaria y equipo, herramientas, muebles y enseres, etc., que determinado nmero de personas desarrollen un esfuerzo mental y material, el cual recibe el nombre de labor, todas las erogaciones por este concepto se consideran dentro de este ttulo. Por lo tanto, se puede decir que: sueldos y salarios es el costo de la retribucin que los obreros u operarios perciben por su trabajo en la transformacin de los materiales en su estado natural o procesado por otra entidad, en un producto terminado (Solo contabilidad, s.f., p. 1).
    h3 Costos indirectos de fabricacin
    p.mb-5 Dentro del proceso productivo, hay costos que no pueden identificarse con unidades especficas de produccin o con un departamento o con un proceso especfico, tales como: la depreciacin de bienes de uso, luz y fuerza, combustibles, lubricantes, repuestos y accesorios, etc.; por lo que se denominan: Servicios de planta, Gastos indirectos de fabricacin, Gastos indirectos de produccin, etc. Es decir, estn constituidos por todos los desembolsos o aplicaciones necesarias para llevar a cabo la produccin y los cuales, por su naturaleza, no son aplicables directamente al costo de un producto en particular (Solo contabilidad, s.f., p. 1).

    h3 Tipos de sistemas de produccin
    p Entre los tipos de sistemas de produccin ms conocidos se encuentran:
    .row.justify-content-around.align-items-center.px-5.mb-4  

      .col-lg.justify-content-md-center.align-self-center

        .ul.lista-ul--color
          li 
            i.fas.fa-check
            p #[strong Bajo pedido:] este se realiza por solicitud directa de un cliente.
          li 
            i.fas.fa-check
            p #[strong Por lotes:] es la produccin que se realiza por una referencia determinada o un producto.
          li 
            i.fas.fa-check
            p #[strong Continua:] por la alta demanda del producto siempre se est fabricando.

    h3 Tipos de costos de produccin:
    .row.justify-content-around.align-items-center.px-5  

      .col-lg.justify-content-md-center.align-self-center      
        .ul.lista-ul--color.mb-5
          li 
            i.fas.fa-check
            p #[strong Costos fijos:] son los costos que hay que pagar en un periodo determinado sin importar si sube o baja la produccin.
          li 
            i.fas.fa-check
            p #[strong Costos variables:] son aquellos que varan segn la produccin si se aumenta o disminuye.
          li 
            i.fas.fa-check
            p #[strong Costo total:] es la suma de los costos fijos y los costos variables.
          li 
            i.fas.fa-check
            p #[strong Costo unitario:] es lo que cuesta producir cada unidad. 
    
    h3 Frmulas costos de produccin
    p Dentro de las frmulas de los costos de produccin encontramos:
    .row.justify-content-around.align-items-center.px-5  

